/* ---- FOOTER ---- */
.footer {
  background: url("~/public/footer.jpg");
  background-size: contain;
  &__bottom {
    margin: 0 13rem;
    color: #abb7c4;
    padding-bottom: 40px;
  }
  &__wrapp {
    margin: 0 13rem;
    &--parent {
      padding: 80px 0 20px 0;
      display: flex;
      align-items: center;
      justify-content: space-between;
      align-items: flex-start;
      h4 {
        font-family: "Dosis", sans-serif;
        font-size: 18px;
        color: #ffffff;
        font-weight: bold;
        text-transform: capitalize;
        margin-bottom: 30px;
      }
      ul li {
        margin-bottom: 10px;
      }
      ul li a {
        font-family: "Nunito", sans-serif;
        font-size: 14px;
        color: #abb7c4;
        text-transform: none;
        text-decoration: none;
        &:hover {
          color: rgb(221, 0, 63);
        }
      }
      ul {
        list-style-type: none;
      }
      .footer__wrapp--item1 {
        width: 15rem;
        &--logo {
          width: 100%;

          .footer__logo {
            width: 80%;
          }
        }
        .footer__namelogo1 {
          background: -webkit-linear-gradient(#fb1818, #fffcfcbd);
          -webkit-background-clip: text;
          -webkit-text-fill-color: transparent;
          font-weight: bold;
          font-size: 25px;
          text-transform: uppercase;
        }
        .footer .footer__wrapp--parent .footer__wrapp--item1 p {
          font-family: "Nunito", sans-serif;
          font-size: 14px;
          color: #abb7c4;
          font-weight: 300;
          text-transform: none;
          line-height: 24px !important;
        }
        .footer .footer__wrapp--parent .footer__wrapp--item1 a {
          color: #ffffff;
          font-size: 18px;
          text-decoration: none;
          /* letter-spacing: 3px; */
        }
        .footer__namelogo2 {
          position: relative;
          font-weight: bold;
          background: -webkit-linear-gradient(#fb1818, #fffcfcbd);
          -webkit-background-clip: text;
          -webkit-text-fill-color: transparent;
          font-size: 25px;
          text-transform: uppercase;
        }
      }
    }
  }
}

/* Responsive mobile */
@media only screen and (max-width: 46.1875rem) {
  /* Footer */
  .footer {
    &__wrapp {
      margin: 0;
      &--parent {
        padding: 30px 0 30px 0;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        .footer__wrapp--item1 {
          p {
            font-size: 16px;
            text-align: center;
          }
        }
        .footer__wrapp--item2 {
          margin-top: 20px;
          text-align: center;
          h4 {
            font-size: 22px;
            margin-bottom: 10px;
          }
          ul {
            display: grid;
            grid-template-columns: 150px 150px;
          }
        }
        .wrapp-item--3n4 {
          display: flex;
          margin-top: 20px;
          column-gap: 40px;
        }
        .footer__wrapp--item3 {
          h4 {
            margin-bottom: 20px;
          }
        }
        .footer__wrapp--item5 {
          margin-top: 20px;
        }
      }
    }
    &__bottom {
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 0;
    }
  }
}

/* Responsive Tablet */
@media only screen and (min-width: 46.25rem) and (max-width: 63.9375rem) {
  .footer {
    &__wrapp {
      margin: 0 1rem;
      .wrapp-item--3n4 {
        display: flex;
        gap: 40px;
        justify-content: center;
        align-items: flex-start;
      }
    }
    &__bottom {
      margin: 0 1rem;
    }
  }
}
